  .post-banner {
    display: none;
    background: #f5f5f5;
    height: 495px;

    .thumb{
      display: none;
      height: 100%;
      width: 100%;
    }

    .thumb.active{
      display: block;
    }
  }

  .post-tabs {
    position: relative;

    .tabs-wrap {
      background: #121212;
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 10px 0;
    }

    .wcf-post {
      flex: 1;
      padding: 30px;
      &:not(:last-child) {
        border-bottom: 1px solid #FFFFFF19;
      }
    }
  }

  .post-grid {
    flex: 1;
    display: flex;
    gap: 60px;
    overflow: hidden;
    flex-direction: column;
    margin-top: 30px;

    .wcf-post {
      position: relative;
      flex: 1;

      &:not(:last-child):after {
        content: "";
        background: #1212121A;
        display: block;
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0;
        bottom: -30px;
      }
    }
  }

  @media (min-width: 992px) {
    .post-banner {
      display: block;
    }
    .post-wrapper {
      display: flex;
    }

    .post-tabs {
      width: var(--tabs-width, 330px);

      .tabs-wrap {
        position: absolute;
        min-height: 100%;
        bottom: 0;
      }
      .thumb {
        display: none;
      }
    }

    .post-grid {
      flex-direction: row;
      margin-left: 30px;

      .wcf-post:after{
        left: auto !important;
        bottom: auto !important;
        width: 1px !important;
        height: 100% !important;
        right: -30px;
        top: 0;
      }
    }
  }
}

//banner post
.wcf__banner-posts{
  .post-wrapper{
    display: flex;
  }

  svg {
    width: 1em;
    height: 1em;
  }
}

.wcf--popup-video .audio {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

ul.posts-filter {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 30px;
}

// content popup 

.aae--filterable-posts.popup-enabled .post-content {
  display: none;
}

.wcf-post .post-thumbnail {
  cursor: pointer;
}

.wcf-post .post-content {
  padding: 15px;
  background-color: #f9f9f9;
  border-top: 1px solid #ddd;
  display: none;
}

.aae-post-popup-content {
  background: #fff;
  padding: 30px;
  height: 700px;
  overflow-y: auto;
}
.aae-post-popup-content .thumb{
  margin-bottom: 20px;
}
.aae-post-popup-content .thumb img{
  width: 100%;
  max-height: 350px;
  object-fit: cover;
}
.aae-post-popup-content .wcf-post-taxonomy {
  margin-bottom: 10px;
}
.aae-post-popup-content .wcf-post-title{
  font-size: 21px;
  margin-bottom: 10px;
  line-height: 1.4;
}
.aae-post-popup-content .wcf-post-meta{
  margin-bottom: 40px;
  font-size: 14px;
}
.aae-post-popup-content .wcf-post-excerpt{
  margin-bottom: 20px;
}
.aae-post-popup-content .wcf-post-excerpt p{
  color: #121212;
  font-size: 16px;
  line-height: 1.5;
}
.wcf-post-link {
  background-color: rgba(255, 173, 45, 1);
  padding: 10px 30px;
  border-radius: 50px;
  font-size: 14px;
}
// content hide 
.aae--filterable-posts.popup-enabled {
  .wcf-post-title{
    display: none;
  }
  .wcf-post-meta{
    display: none;
  }
  .wcf-post-excerpt{
    display: none;
  }
  .wcf-post-taxonomy{
    display: none;
  }
  .wcf-post-link{
    display: none;
  }
}

// widget default responsive 
@media (max-width:1500px){
  .aae-post-popup-content .thumb img{
    max-height: 300px;
  }
  .aae-post-popup-content {
    height: 600px;
  } 
}
@media (max-width:1024px){
  .aae-post-popup-content .thumb img{
    max-height: 300px;
  }
  .aae-post-popup-content {
    height: 450px;
  } 
}
@media (max-width:768px){
  .aae-post-popup-content .thumb img{
    max-height: 150px;
  }
  .aae-post-popup-content {
    height: 250px;
    padding: 15px;
  } 
}